body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --header-font-header5-medium: "PingFang SC";
  --font-inherit: inherit;

  /* font sizes */
  --header-font-header5-medium-size: 16px;
  --description-font-description-medium-size: 12px;
  --header-font-header2-medium-size: 28px;
  --header-font-header4-medium-size: 20px;
  --header-font-header3-medium-size: 24px;
  --header-font-header1-medium-size: 38px;

  /* Colors */
  --bg-color-bg1-1: #fff;
  --border-color-border1-1: #e3e5ed;
  --text-gray-color-text1-3: #747c94;
  --decorative-purple-color-decorative4-1: #915aff;
  --text-gray-color-text1-2: #202241;
  --text-gray-color-text1-1: #0a1032;
  --fill-color-fill1-1: #f6f6f9;
  --status-warning-color-warning-fill: #fdefea;
  --status-warning-color-warning-default: #f27e19;
  --status-success-color-success-fill: #e9f6f1;
  --status-success-color-success-default: #129b76;
  --color-aliceblue: #f6fbff;
  --status-invalid-color-invalid-fill: #ebedf3;
  --status-info-color-info-fill: #eaeefd;
  --status-info-color-info-default: #1e76f0;
  --status-danger-color-danger-fill: #fbebea;
  --status-danger-color-danger-default: #e84d37;
  --secondary-color-secondary-transp-fill: rgba(185, 190, 207, 0.3);

  /* Gaps */
  --gap-xs: 12px;
  --gap-base: 16px;
  --gap-9xs: 4px;
  --gap-31xl: 50px;
  --gap-5xl: 24px;

  /* Paddings */
  --padding-61xl: 80px;
  --padding-81xl: 100px;
  --padding-29xl: 48px;
  --padding-101xl: 120px;
  --padding-12xs: 1px;
  --padding-5xs: 8px;
  --padding-45xl: 64px;
  --padding-5xl: 24px;
  --padding-base: 16px;
  --padding-8xl: 27px;

  /* border radiuses */
  --br-11xs: 2px;
  --br-xs: 12px;

  /* Effects */
  --border-inside-bottom: 0px -1px 0px #e6e6e6 inset;
}
